body {
  font-family: Titan One, cursive;
  color: #000000;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link-style {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  }